body {
    background: #00ff89;
    font-family: "Lucida Console", Courier, monospace;
}

.title {
    text-align: center;
}

.content {
    display: flex;
    width: 100%;
    height: 800px;
}

.subcontent {
    width: 50%;
}

.leftSubcontent {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}

.pane {
    margin: 10px;
    height: 40%;
}

.paneHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.paneHeader div {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.paneHeader div h2, .paneHeader h2 {
    margin: 0px;
}

.runButton {
    cursor: pointer;
    padding: 3px 10px;
    border-radius: 3px;
    border: 1px solid;
    margin: 0 10px;
}

.runButton:hover {
    background: #049681;

}

.paneContent {
    height: 90%;
    background: #00bba1;
    padding: 5px;
    border: 1px solid;
    border-radius: 5px;
    overflow: auto;
}

.elementName {
    cursor: pointer;
    width: -moz-fit-content;
    width: fit-content;
    padding: 3px;
    border-radius: 3px;
    border: 1px solid;
}

.elementName:hover {
    background: #049681;
}

.textEditorPaneContent {

}

.errorPaneContent {
    border: 3px solid;
    margin: 0;  
    animation: errorPaneBorderBlink 0.5s step-end infinite;  
}

.errorMessage {
    color: #1c3a35;
}

@keyframes errorPaneBorderBlink {    
    from, to {    
        border-color: #90f3e5
    }    
    50% {    
        border-color: #1c3a35    
    }    
}  

.textEditor {
    height: 97%;
    width: 99%;
    resize: none;
    outline: none;
    border: none;
    background: #00bba1;
}

.resultPane {
    height: 90%;
}